.works {
  /*grid-area: w;*/
  /*grid-gap: 10px;*/
  /*margin: 10px 10% 10px 10%;*/
  display: flex;
  flex-direction: column;
  align-items: center;
  min-height: 100vh;
}
.container {
  background-color: rgb(35%, 35%, 35%);
  min-height: 80vh;
  width: 90%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-end;
  color: lightgreen;
  border: 1px solid;
}

.wrap {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-around;
  border: 1px solid;
  min-height: 70vh;
  width: 100%;
  flex-wrap: wrap;
}

.title {
  height: 50px;
  text-align: center;
  margin: 20px;
  color: white;
  justify-content: space-evenly;
  align-items: center;
}

.block {
  display: flex;
  flex-direction: column;
  height: 450px;
  color: green;
  /*font-size: 12px;*/
  align-items: center;
  justify-content: flex-start;
  /*flex-wrap: wrap;*/
  background-color: paleturquoise;
  /*color: cyan;*/
  border: 1px solid;
}

.img {
  display: flex;
  height: 320px;
  width: 320px;
  /*border-radius: 50%;*/
  background-color: linen;
  justify-content: center;
  align-items: center;
  /*margin: auto;*/

}